.honeycomb-container{
  background-color: rgba(0, 0, 0, 1);
  position: absolute;
  width: 100%;
  z-index: 3;
}

.honeycomb {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
  align-items: center;
  max-width: 900px;
  margin: 0 auto;
  padding: 0;
  z-index: 3;
}

.honeycomb-cell {
  flex: 0 1 250px;
  /* max-width: 250px; */
  height: 137.5px;
  margin: 65.4px 12.5px 25px;
  position: relative;
  padding: 0.5em;
  text-align: center;
  z-index: 3;
  box-shadow: 0px 0px 15px 0 rgba(0, 0, 0, 0.1);
}

.honeycomb-cell-img {
  object-fit: cover;
  object-position: center;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.honeycomb-cell-title {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
      hyphens: auto;
  word-break: break-word;
  text-transform: uppercase;
  color: #fff;
  font-weight: 700;
  font-size: 1.75em;
  transition: opacity 350ms;
}

.honeycomb-cell::before,
.honeycomb-cell::after {
  content: "";
}

.honeycomb-cell::before,
.honeycomb-cell::after,
.honeycomb-cell-img {
  top: -50%;
  left: 0;
  width: 100%;
  height: 200%;
  display: block;
  position: absolute;
  -webkit-clip-path: polygon(
    50% 0%,
    100% 25%,
    100% 75%,
    50% 100%,
    0% 75%,
    0% 25%
  );
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  z-index: -1;
}

.honeycomb-cell::before {
  background: #fff;
  transform: scale(1.055);
}

.honeycomb-cell::after {
  background: #111111;
  opacity: 0.7;
  transition: opacity 350ms;
  -webkit-transition: opacity 350ms;
}

.honeycomb-cell:hover .honeycomb-cell-title {
  opacity: 70%;
}

.honeycomb-cell:hover .honeycomb-cell-img {
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
}

.honeycomb-cell:hover::before {
  background: #f5a904;
}

.honeycomb-cell:hover::after {
  opacity: 0;
}

.honeycomb-hidden {
  display: none;
  opacity: 0;
  width: 250px;
  margin: 0 12.5px;
}

